<template>
  <div class="Link">
    <div class="bar">
      <input id="input" v-model='message' @keyup.enter="direct(message)" placeholder="Paste the share link"/>
      <button id="btn" @click="direct(message)">
        Direct
      </button>
    </div>
    <p id="tip">
      (only support personal share link)
    </p>
  </div>

</template>

<script>
export default {
  name: "Link",
  props: {
    message: {
      type: String,
      default: ''
    }
  },
  // data() {
  //   let message = ''
  //   // eslint-disable-next-line vue/no-dupe-keys
  //   return { message }
  // },
  methods: {
    direct(msg) {
      if (msg !== '') {
        window.location.href = msg.replace('https://', '').replace('1drv.ms/', '')
      }
    }
  },
}
</script>

